import React from 'react'

export default class Aruanne extends React.Component {
  render() {
    return <div className="mt-3">
      <h4>Projekti lähteülesande, eesmärgi kirjeldus</h4>

      <p> Projekti esmaseks eesmärgiks, nagu ka pealkiri ütleb, on välja selgitada kuidas hindavad Tallinna lapsed ja lapsevaneamad oma koolitee turvalisus. Lisaks püüame saada väärtuslikku infot, milliseid liikumisviise kasutavad lapsed kooli minemiseks ja sealt tulemiseks. Tõusva trendina on täheldatud, et lastevanemate seas on kombeks tuua lapsi kooli isikliku autoga. Keskendume lähemalt selle valiku põhjuste tagamaadele. Proovime leida vastuseid küsimusele, mis mõjutaksid lapsi ja lapsevanemaid tervislikemaid ja keskonnasõbralikemaid liikumisviise valima. Mida saab selleks teha Tallinna linn, et koolide ümbruses muutuks liikluskeskkond turvalisemaks ning tervise- ja keskonnasõbralikumad liikumisviisid muutuksid esmasteks valikuteks? </p>
      <p> Projekti tellijaks on Tallinna Linnavalistus ja saadud tulemused edastatakse linna uue arengukava koostamiseks.</p>

      <h4>Probleemi olulisus, kirjeldus ja meetodite valik</h4>

      <p>Esmaseks probleemiks tooksime välja liiklusõnnetuste rohkuse. On selge, et koolide ümbrus on lastega liiklusõnnetuste toimumise riskipiirkond, kuna sinna koondub suur osa lastega seotud liikumistest. Seetõttu on kindlasti vajalik määrata kindlaks just koolide ümbruses aset leidnud liiklusõnnetuste riskitegureid. </p>
      <p>Tallinna Järeveotsa Gümnaasium kvalifitseerub kvartalissiseselt suhteliselt väikse liiklussagedusega kuid reguleerimata liikluskorralduse ja lahendamata parkimiskorralduse tõttu suureneb liiklusoht märgatavalt. Samuti on probleemiks liikluse rahustamisvõtete puudumine, mistõttu juhid ei taju liiklusolukorda kui jalakäijate liiklust soosivat. Selle tulemusena kasutatakse suuri sõidukiirusi.  </p>
      <p> Ohvrite arv liiklusõnnetustes on  viimastel aastatel kahanenud, kuid võrreldes teiste arenenud riikidega on näitaja endiselt kõrged. ( Liiklussurmade statistika EL-s infograafik)  Uuringud näitavad, et kellaaegade lõikes juhtub kooliealiste lastega enim liiklusõnnetusi hilisõhtusel ajal. Sellele järgneb õhtune tipptund. Kui aga vaadelda hommikust tipptundi ( kell 7-8) siis on see märgatavalt ohtlikum kui eelnev ja järgnev tund. Seda saab otseselt seostada laste koolimineku ajaga. (Tallinna arengukava „Turvaline Kooolitee“ 2008-2013) </p>
      <p>Aastal 2001 oli Eestis kõige rohkem liikluses hukkunud lapsi miljoni elaniku kohta Euroopas. Aastaks 2017 olid küll Eestis liiklussurmad vähenendud 36% võrra kuid Euroopa Liidus oleme siiski kõrgel kuunedal kohal ( Road safety 2017, ec.europa.eu). Üheks põhjuseks on kindlasti liiklussageduse kasv, mis on võrdeline autode arvu kasvuga, mis omakorda tingib pingelisemad liiklussituatsioonid. </p>
      <p>Liiklusõnnetusega puututakse kokku tihtipeale just koolide  lähedused. Kasutatava ühistranspordi puhul ei saa kindel olla nende tehnilises seisukorras, liikumisgraafikus ning meedias on kajastatud ka olukordi, kus juhi pädevus tekitab küsimusi. </p>
      <p> Tõenäoliselt on neid kooliõpilasi, kes eelistaksid koolis käia jalgratta või muu motoriseerimata liiklusvahediga, kuid takistuseks saab just puudulik või olematu infrastruktuur. (Inseneribüroo Stratum, 2003). </p>

      <p>Et jõuda ühise objektiivse lahenduseni, mis sisaldaks endas nii liiklusohutust, füüsilise aktiivsuse võimalust kui oleks ka oleks säästev keskkonna suhtes, tuleks märgatavalt ümber korraldada infrastruktuuri. See on aga ulatuslik protsess nii ajaliselt kui ka rahaliselt ning ilmneda võivad erinevad takistused ka olemasolevate tehniliste lahenduste näol. Probleemist lähtudes, on oluline pakkuda lahenduskäike, mis võimaldaksid minimaliseerida ohu taset kooliteel. Selleks tuleb välja selgitada ja kaardistada võimalikud ohukohad. Ühtlasi teavitada nii õpilasi, kui ka lastevanemaid paikadest, kus tuleks olla eriti ettevaatlik. Ohtude määratlemist ja liiklusskeemi koostamist peetakse otstarbekaks meetodiks ning see toob kaasa tarvilikku informatsiooni (Inseneribüroo Stratum, 2003). </p>
      <p>Rahvastiku Tervise Arengukava 2009-2020 andmetel ei ole  liikluses hukkunute arv viimaste aastate jooksul vähenenud vaid pigem peatunud. Sellest võib järeldada, et lapsevanemad tajuvad liikluskeskkonda ohtlikuna ja see sunnib neid oma lapsi autoga kooli viima ja koju tooma. Järveotsa kooli vahetus läheduses oli 2017 aastal raske avarii, kus sõiduauto rammis bussiootepaviljoni ja viga sai seitse inimest, sealhulgas mitu koolilast. Eelnimetatud liiklusõnnetus sai laialdast meediakajastust, mis veelgi süvendas lapsevanemates veendumust nende laste koolitee ohtlikkusest. </p>

      <p> Teiseks suureks probleemiks on liikumise drastiline vähenemine kooliealiste hulgas. Üha rohkem kasvavad terviseprobleemid, mida seostatakse eelkõige vähese füüsilise aktiivsusega. Mitte kõik õpilased ei ole suutelised järgima WHO soovitusi lastele vanuses 5-17,  liikuda päevas minimaalselt 60 minutit. 2015 a TÜ Liikumislabori andmetel on vaid 24% õpilaste liikuisaktiivsus piisav. Füüsiliselt aktiivne olemine ei vaid ühe indiviidi valik ja vastutus. Otsuseid mõjutavad tegurid on näiteks infrastruktuur, kommunikatsioonide võrgustikud, sotsiaalsed, ja üldised sotsiaal-majanduslikud, kultuurilised ja keskkondlikud tingimused. </p>
      <p> Lisaks vanematele saaks ka koolipersonal mõjutada nii lapsi kui kohalikku omavalitsust tervislikumale liikumisele. Omavalitstus,  korraldades turvaliselt ümber kooliteid, saab soodustada õpilaste aktiivset liikumist kooli ja koju.</p>
      <p>Tervise heaolu aspektist vaadatuna võib öelda, et lapsele valitud koolitee mõjutab liikumisharjumusi kas positiivsel või negatiivsel moel. Kasvav probleem füüsilise liikumise vähesusega näitab, et eelnimetatud teema on aktuaalne, kuna üha rohkem lapsevanemaid eelistab võimalusel lapsi autoga kooli viia. Viimase tulemuseks on aga laste liikumisvähesus, terviseprobleemid ja ülekaalulisus (Mooses, 2017), (Inseneribüroo Stratum, 2003). </p>
      <p> Toitumise ja liikumise rohelises raamatus (2016) on välja toodud, et Eesti võrreldes teiste Euroopa Liidu liikmesriikidega,  kuulub täiskasvanud elanike ebapiisava kehalise aktiivsuse poolest (alla 150 minuti keskmise intensiivsusega tegevust nädalas) nende riikide hulka, kus liikuvus on pigem väike (sotsiaalministeerium 2016). Vähene liikumine lapse kasvu- ja arenguperioodil võib põhjustada mitmeid terviseprobleeme. Näiteks ei ole ebapiisava liikumise tagajärjeks vaid puudulik liigutuslik pagas ja kehatunnetus, vaid häired ilmnevad ka hingamiselundkonnas ja südame-veresoonkonnas (Aasvee et al. 2012). WHO soovituse kohaselt peaksid lapsed liikuma vähemalt 60 minutit päevas, harrastades keskmise kuni kõrge intensiivsusega tegevusi (WHO 2010). Liikumise harrastamine üle 60 minuti päevas on seotud täiendavate tervisekasudega (Sotsiaalministeerium 2016, Tammelin, T. 2008 kaudu). Suur osa Euroopa, sh Eesti elanikkonnast ei saavuta aga päevast soovitavat liikumise taset. Lisaks esineb selge liikumisaktiivsuse märgatava vähenemise trend 11–15- aastaste noorukite seas. </p>
      <p> Tasakaalustamata toitumine ja vähene liikumine on peamised riskitegurid ülemäärase kehakaalu tekkes. Regulaarne liikumine on oluline normaalseks kasvuks ning on seotud vaimse heaoluga ning parandab lastel ja noorukitel enesekindlust. Samuti esineb kehaliselt aktiivsetel lastel ja noorukitel vähem vaimse tervisega probleeme. Vajalikku liikumissagedust on raske saavutada, kuna lapsed on muutunud vähem aktiivseks ning üha enam harrastatakse istuvaid huvitegevusi ja ekraanile suunatud tegevusi, mis on nn istumist soodustavad tegurid. </p>
      <p>Võimalused, mis on kogukonnas, mõjutavad laste liikumist. Keskkond, mis soodustab kooli või trenni liikumist jalgsi või jalgrattaga, on oluline komponent laste liikumisaktiivsuse edendamisel. Seejuures on oluline turvalisus, sest vanemate mure laste turvalisuse pärast liikluses on üks põhjus, miks nad ei soovi, et lapsed jalgsi või jalgrattaga kooli läheksid. Keskkond, mis aitab edendada laste kehalist aktiivsust, on oluline komponent võimaldamaks lastel saavutada soovitatav liikumistase (Tester J. et al. 2009). </p>
      <p>Liikumine peaks lapsele olema nauditav ja pakkuma liikumisrõõmu ning sellel ei tohiks olla karistuslik eesmärk. Lastele tuleb anda  edasi teadmisi liikumise ja tervise vahelistest seostest ning arendada oskusi liikumise elukestvaks harrastamiseks. </p>
      <p>Kolmandaks puudutame autostumise probleemi, millel on tugev seos kahe eelneva valupunkti tekkepõhjusega. Lisandub veel suur koormus keskkonnale heitgaaside ja müra näol. Siinkohal on taaskord suurimaks kannatajaks just lapsed ja vanemaealised. (Pealinn, 07.06.2019) On selge, et ei piisa ainult ühele valupunktile leeveduse või lahenduse otsimisest vaid tuleb vaadata suurt pilti korraga ja ka lahendus tuleb leida süsteemselt. </p>
      <p>Selleks, et eelnimetatud probleemide lahendustele lähemale jõuda, leidsime, et tuleb pöörduda nende poole, kes valukohti kõige selgemalt tunnevad ja tajuvad. Sihtgrupiks sai valitud koolide õpilased ja nende vanemad. Erinevate alternatiivide kaalumisel jõudsime otsusele, et kõige lihtsam, kiirem ja mugavam  tee tänasel infoajastul on elektroonilise küsimustiku koostamine ning edastamine sihtgrupile. Maskimaalselt otsene kontakt probleemide keskmes oleva sihtgrupiga,  samuti anonüümsus, loovad head eelduse maksimaalselt realistlike vastuste tarvis. </p>

      <h4>Tegevuse kirjeldus ja sidusgruppideni jõudmine </h4>

      <p>Enne lõplike küsimustike koostamist otsustasime teha  testintervjuud, et jõuda selgusele, kas meie valitud suund ja küsimused on õige ja mida tasuks lõplikus küsimustikus  muuta ning millele rohkem või vähem tähelepanu pöörata. Kõik kolm gruppi tegid läbi intervjuu ühe lapsevanemaga grupikohtumistel arutluse all olnud ja valituks osutunud küsimustega. Vastused näitasid, et oleme õigel teel. Ostsutasime koostada sarnase küsimustiku nii lastele kui lastevanematele, et saada võimalikult lai ja adekvaatne pilt, kuidas mõlemad pooled tajuvad olukorda tõstatatud küsimustes. </p>
      <p>Koolide valimisel said grupid  vabad käed ja valitusk osutusid koolis, näitasid huvi käesoleva projekti vastu ning olid aktiivselt valmis igakülgseks koostööks.</p>
      <p>Projektis osalevad kolm kooli: Tallinna Inglise Kolledž, Tallinna Järveotsa Gümnaasium ja Kadrioru Saksa Gümnaasium. Meie grupi kooliks sai Järveotsa Gümnaasium ja sihtgrupiks seal õppivad lapsed ja nende vanemad.</p>

      <h4>Jätkusuutlikus</h4>
      <p>Projekt “Turvaline koolitee” ei ole esmakordselt toimuv. Sellenimelist ühisprojekti on ka varasemalt läbi viidud ning nii mitmedki tulemused on jõudnud Tallinna Linnavalitsuse liikluskorralduse osakonnani. Ühtlasi on projektides kajastunud informatsiooni kasutatud Tallinna linna arengukavades. Selleks, et info koolide ümbruses asuvate ohukohtade kohta oleks objektiivne ja värske, on oluline projekti teatud intervalliga uuesti läbi viia. Linna infrastruktuur ja liiklussituatsioonid muutuvad, sellega koos ka kooliteede turvalisuseaste. Ka käesoleva projekti raames kogutud andmed edastatakse Tallinna Linnavalitsusele ja materjali sobivuse korral võetakse projekti materjalid kasutusele uue arengukava loomisel. </p>

    </div>
  }
}